
.button85 {
  padding: 0.4em 1.6em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button85:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button85:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(48, 48, 48);
  left: 0;
  top: 0;
  border-radius: 10px;
}

.cartDiv {
  overflow-y: auto;
  height: auto;
  max-height: 40vh;
}
.userForm {
  overflow-y: auto;
  height: auto;
  max-height: 12rem;
}
.userForm form input:focus {
  box-shadow: none !important;
}

.invalid {
  border: 1px solid rgb(255, 8, 8) !important;
  background-color: #ffa0a05c !important;
}
.valid {
  /* border-color:#146c43 !important; */
  background-color: #d1e7dd !important;
}

.btnInvalid {
      display: inline-block;
  cursor: not-allowed !important;
  padding:0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  position: relative;
    flex: 1 1 auto;
    border-radius: 0.375rem;
        border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border:1px solid lightgrey;
}
